import React, { Component } from 'react';

import { Layout, Menu, Dropdown, Icon } from 'antd';
import './caro.css';
import crown from './crown.png'








export default class Caro extends Component {

  
myFunction() {
  var x = document.getElementById("myTopnav");
  console.log("aa",x)
  if (x.class === "caro") {
    x.class += " responsive";
  } else {
    x.class = "caro";
  }
}

  render() {

    return (


      <div>

        <div class="caro" id="myTopnav">
          <div className="">
            <img className="crown" src={crown} />
          </div>

          <div className="caros"><a href="#brand">|OUR BRANDS|</a></div>
          <div className="caros"><a href="#product">|PRODUCTS|</a></div>
          <div className="caros"><a href="/gall">|GALLERY|</a></div>
          <div className="caros"><a href="#contact">|CONTACT US|</a></div>

          <div className="caros2"><Icon type="user" /></div>
          <div className="hide">
          <a href="javascript:void(0);" class="icon" onClick={this.myFunction}>
            <i class="fa fa-bars"  style={{fontSize:"32px"}}></i></a>
            </div>
         
        </div>
      </div>
        )
      }
    }



