import React,{Component} from 'react';
import a from './pdt.jpg';
import c from './shoes.jpg';
import d from './watch.jpg';
import e from './shirt.jpg'; 
import './card3.css'
export default class Product extends Component{
    render(){

        return(
            <div className="card">
            <div className="pdt"><u>Our</u> Products</div>
            <br/>
            <br/>
                <div className="display">
                 <div className="inside">
                 <div><img className="image" src={a}/></div>
                 <div className="textblock">S<br/>H<br/>O<br/>E<br/>S</div>
                 </div>
                 <div  className="inside">
                 <div><img className="image" src={c}/></div>
                 <div className="textblock">S<br/>H<br/>A<br/>D<br/>E<br/>S</div>
                 </div>
                 <div  className="inside">
                 <div><img className="image" src={d}/></div>
                 <div className="textblock">W<br/>A<br/>T<br/>C<br/>H<br/>E<br/>S</div>
                 </div>
                <div  className="inside">
                <div><img className="image" src={e}/></div>
                 <div className="textblock">S<br/>H<br/>I<br/>R<br/>T<br/>S</div>
                </div>
                </div>



            
            </div>
        )
    }
}


