import React, { Component } from 'react';
import './card4.css';
import ma from './map.jpg'
export default class Contact extends Component {

    render() {

        return (

            <div className="content">
                <div className="posse">Contact <u>Us</u></div>
                <br/>
                <br/>
                <div className="contact">
                    <div className="email" >
                    <div>
                     <div className="email">
                            <i class="fa fa-envelope-o" ></i>
                            <div className="font"> E mail</div>
                    </div>
                        
                            <div>happytohelp@gmail.com</div>
                            <div>hhlakd@help.com</div>
                        </div>

                        <div>
                        <div className="email">
                            <i class="fa fa-phone" style={{ fontSize: "32px",textAlign:"center"}} ></i>
                            <div className="font"> Call Us</div>
                        </div>
                        
                            <div>Mobile:+800764690</div>
                            <div>+180447924</div>
                        </div>
                    </div>
                    <br/>
                    <br/>
                     <div className="email">
                     <div>
                     <div className="email">
                            <i class="fa fa-info" style={{ fontSize: "32px",textAlign:"center"}}></i>
                            <div className="font">FAQs</div>
                     </div>
                        
                            <div>you can find answers for all your queries.</div>
                        </div>
                        <div>
                            <div className="email">
                              <i class="fa fa-commenting-o" style={{ fontSize: "32px",textAlign:"center"}}></i>
                              <div className="font">Comments</div>
                            

                            </div>
                            Comment here.
                      </div>

                    </div>
                   



                </div>
                <div  >
                        <img className="map"  src={ma}/>
                    </div>



            </div>
        )
    }
}


