import React, {Component} from 'react';
import Ca from './caro.js';
import Car from './card.js';
import Cars from './card2.js';
import Product from './card3.js';
import Contact from './card4.js';
import Foot from './footer.js';
import Links from './end.js';
import Gall from './Gallery.js';
import Home from './home.js';
import Get from './get.js'
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom' 

export default class Go extends Component{

    render(){

        return(

            <div>
                <Ca/>

                <Router>
                    <Switch>
                        <Route path="/gall" component={Gall}/>
                        <Route path="/" component={Get}/>
                    </Switch>
                </Router>
                
                <Links/>
            </div>
        )
    }
}


