import React, {Component} from 'react'
import './caro.css'
import a from './lp7.jpg'
import c from './lp2.jpg'
import d from './lp5.jpg'
import e from './lp4.jpg'
import f from './logo.png'
import { Carousel } from 'antd';

export default class Home extends Component{

    render(){
        return(

            <div style={{ zIndex: '0' }}>
            <Carousel autoplay>

              <div className="contain">
                <img className="con1" src={a} />

              </div>
             
              <div className="contain">
                <img className="con1" src={c} />

              </div>
              <div className="contain">
                <img className="con1" src={d} />

              </div>
              <div className="contain">
                <img className="con1" src={e} />

              </div>
            </Carousel>

          </div>




          

            
        )
    }
}


