import React,{Component} from 'react';
import a from "./g1.jpg"
import b from "./g2.jpg"
import c from "./g3.jpg"
import d from "./g4.jpg"
import e from "./g5.jpg"
import f from "./g6.jpg"
import './gallery.css'

export default class Gallery extends Component{

    render(){

        return(
          <div className="background">
              <div className= "fam">GALLERY</div>
             <div className="alignment">
                <div><img src={a}/> </div>
                <br/>
                <div><img src={f}/> </div>
                <br/>
                <div><img src={b}/> </div>
                <br/>
                <div><img src={c}/> </div>
                <br/>
                <div><img src={d}/> </div>
                <br/>
                <div><img src={e}/> </div>
                
            </div>
          </div>
          
        )
    }
}


