import React, { Component } from 'react';
import a from './jeans.jpg';
import b from './lp.jpg';

import './card2.css';
export default class Brand extends Component {

    render() {

        return (
            <div className="el">
                <div className="style">
                  <u>Our</u>  Brands 
                 <br/>
                 <br/>
                </div >
                <br/>
                <div className="element">
                 <div><img className="ele" src={a} /></div>
                
                 <div ><img className="ele" src={b} /></div>
                 
                 <div><img className="ele" src={a}/></div>
                 
                 <div><img className="ele" src={b} /></div>
                 
                 
                </div>
               

            </div>

        )
    }
}


