import React, { Component } from 'react';
import { Card } from 'antd';
import Column from 'antd/lib/table/Column';
import ta from './tail.jpg';
import ma from './ma.jpg';
import ex from './expo.jpg'
import './card.css'

export default class Car extends Component {

  render() {

    return (
      <div >
        <div className="card1">
          <div className="next">Louis Phillipe-In Pursuit of Excellence</div>
         <br/>
          <div className="def">
            <div className="head">
              <h style={{fontSize:40}}>D</h>efining class, elegance and status, Louis Philippe is a purveyor of fine clothing for the discerning gentleman. In pursuit of excellence, Louis Philippe stands for precision in craftsmanship that translates into clothing that are classic and contemporary, and perfect for the quintessential man. An international super brand for menswear clothing, Louis Philippe offers a range of formals, semi-formals, custom-made clothing and accessories. Addressing the needs of the style-conscious man, shirts, trousers, T-shirts and accessories that can be bought in-store and online.
            </div>
            <div >
             <div >
               <img className="with" src={ta}/>
             </div>
             
             

            </div>
          </div>
        </div>




      </div>
    )
  }


}



